		//content
		#container{}
		#content{}
		.content-wrap{
			&__outer{}
			&__inner{}
			.swiper{padding: 160px 0 0;height: 768px;position: relative;box-sizing:border-box;
				&-container{width: 100%;}
				&-wrapper{}
				&-controller{
					.btn-swiper-prev{position: absolute;top: 325px;left: 10px;width: 44px;height: 72px;background: url("/assets/images/club_house_sing_along/btn_prev01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;z-index: 2;
						&.swiper-button-disabled{opacity: 0;}
					}
					.btn-swiper-next{position: absolute;top: 325px;right: 10px;width: 44px;height: 72px;background: url("/assets/images/club_house_sing_along/btn_next01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;z-index: 2;
						&.swiper-button-disabled{opacity: 0;}
					}
				}
				&-slide{padding: 22px 0 14px;z-index: 1;}
			}
			.clubhouse-sing-along{
				$bgListPosX : (-18px, -7px, -15px, -12px, -11px);
				$bgListActivePosX : (-42px ,-40px ,-44px ,-50px ,-44px);
				&__area{}
				&__box{}
				&__bg{}
				&__items{width: 826px;height: 479px;margin: 0 auto;display: flex;align-items: center;justify-content: center;flex-wrap: wrap;
					&.length1{
						.clubhouse-sing-along__item{
							&:nth-of-type(1){margin: 0 56px;}
							&:nth-of-type(2){display: none;}
							&:nth-of-type(3){display: none;}
							&:nth-of-type(4){display: none;}
							&:nth-of-type(5){display: none;}
						}
					}
					&.length2{
						.clubhouse-sing-along__item{
							&:nth-of-type(1){margin: 0 56px;}
							&:nth-of-type(2){margin: 0 56px;}
							&:nth-of-type(3){display: none;}
							&:nth-of-type(4){display: none;}
							&:nth-of-type(5){display: none;}
						}
					}
					&.length3{
						.clubhouse-sing-along__item{
							&:nth-of-type(1){margin-right: 68px;}
							&:nth-of-type(2){margin-right: 68px;}
							&:nth-of-type(3){margin-right: 40px;}
							&:nth-of-type(4){display: none;}
							&:nth-of-type(5){display: none;}
						}
					}
					&.length4{
						.clubhouse-sing-along__item{
							&:nth-of-type(1){margin: 0 56px;}
							&:nth-of-type(2){margin: 0 56px;}
							&:nth-of-type(3){margin: 27px 56px 0;}
							&:nth-of-type(4){margin: 27px 56px 0;}
							&:nth-of-type(5){display: none;}
						}
					}
					&.length5{
						.clubhouse-sing-along__item{
							&:nth-of-type(1){margin-right: 68px;}
							&:nth-of-type(2){margin-right: 68px;}
							&:nth-of-type(3){margin-right: 40px;}
							&:nth-of-type(4){margin: 27px 87px 0 0;}
							&:nth-of-type(5){margin: 27px 0 0;}
						}
					}
				}
				&__item{position: relative;width: 205px;height: 226px;display: block;transform: scale(1);transition: transform 0.3s;
					&:before{content:"";position: absolute;top: 0;z-index: -1;transform: scale(1);transition: right 0.2s $ease-in-cubic;}
					&.active{
						&:not(.disabled){
							&:not(.vacation) {transform: scale(1.05);
								@for $i from 1 through 5 {
									&.bg-color0#{$i}{&:before{right: nth($bgListActivePosX, $i);}}
								}
							}
						}
					}
					@for $i from 1 through 5 {
						&.bg-color0#{$i}{
							&:before{right: nth($bgListPosX, $i);@include sprite(nth($club-house-sing-along-bgitem1-sprites, $i));}
							.btn{background: url("/assets/images/club_house_sing_along/bg_item0#{$i}_01.png") no-repeat 0 0;}
						}
					}
					&.default{}
					&.disabled{
						.tit-week,
						.txt-info{opacity: 0.4;}
					}
					&.vacation{
						&:before{display: none;}
						.tit-week{color: #d4dde1;}
						.img{display: none;}
						.btn{background-image: url("/assets/images/club_house_sing_along/bg_item_vacation.png");}
					}
					&.complete{}
					&.this-week{
						&:after{content:"";position: absolute;top: -21px;right: -58px;width: 97px;height: 117px;
							animation: note-icon 0.6s steps(5) infinite forwards;background: url("/assets/images/sprite/club_house_sing_along_note1.png") no-repeat 0 0;
						}
						@for $i from 1 through 5 {
							&.bg-color0#{$i}{&:before{right: nth($bgListActivePosX, $i);}}
						}
						.tit-week{color: #fff509;}
						.txt{
							&-info{color: #fff509;}
						}
						.icon{
							&-txt-week{width: 77px;height: 24px;background: url("/assets/images/club_house_reader/txt_week01_active.png") no-repeat 0 0;}
							&-num{
								@for $i from 1 through 30 {
									&#{$i}{display: inline-block;@include sprite(nth($club-house-num-active-sprites, $i));}
								}
							}
						}
					}
					.btn{display: block;width: 100%;height: 100%;}
					.tit-week{position: absolute;top: 0;left: 0;width: 100%;padding-top: 10px;height: 30px;display: flex;align-content: center;justify-content: center;@include font($nsroundecp, $font-b, 30px, 30px, #fff);letter-spacing: 1px;
						.number-box{margin: 3px 0 0 10px;}
					}
					.img{position: absolute;top: 41px;left: 12px;overflow: hidden;width: 178px;height: 124px;border-radius: 14px;}
					.icon{
						&-txt-week{width: 77px;height: 24px;background: url("/assets/images/club_house_reader/txt_week01.png") no-repeat 0 0;}
						&-num{
							@for $i from 1 through 30 {
								&#{$i}{display: inline-block;@include sprite(nth($club-house-num-sprites, $i));}
							}
						}
					}
					.txt{
						&-box{position: absolute;bottom: 2px;left: 11px;height: 56px;width: 178px;display: flex;align-content: center;justify-content: center;flex-direction: column;overflow: hidden;}
						&-info{width: 170px;padding-bottom: 3px;@include font($comic, $font-b, 22px, 26px, #fff);@include multi-text-overflow(2);}
					}
				}
			}
			.function{
				$iconList : theme, phonics, holiday;
				&__area{position: absolute;top: 100px;right: 23px;height: 210px;z-index: 3;}
				&__box{}
				&__bg{}
				&__items{}
				&__item{position: relative;width: 70px;height: 70px;display: block;transform: scale(1);transition: transform 0.3s;
					@each $item in $iconList {
						&.active{
							.icon{
								&-#{$item}{background: url("/assets/images/club_house_sing_along/icon_#{$item}01_active.png") no-repeat 0 0;}
							}
						}
						.icon{
							&-#{$item}{background: url("/assets/images/club_house_sing_along/icon_#{$item}01.png") no-repeat 0 0;transition: all 0.3s}
						}
					}
				}
				.icon{display: block;width: 100%;height: 100%;}
				.btn{display: block;width: 100%;height: 100%;}
			}
		}
	//}
