@import "../mixin/baseImport";
/*theme songs*/
.sing-along01{position: absolute;top: 0;left: 50%;width: 826px;height: 768px;margin-left: -314px;z-index: 1;
	.tab{position: absolute;top: 96px;left: -140px;width: 140px;
		&__items{}
		&__item{text-align: right;
			$iconList : theme, phonics, holiday;
			@each $item in $iconList {
				&.default{
					.btn{
						&-#{$item}{width: 66px;}
					}
					.icon{
						&-#{$item}{background: url("/assets/images/club_house_sing_along/popup/icon_#{$item}01.png") no-repeat 0 0;
						}
					}
				}
				&.active{
					.btn{
						&-#{$item}{width: 140px;}
					}
					.icon{
						&-#{$item}{background: url("/assets/images/club_house_sing_along/popup/icon_#{$item}01_active.png") no-repeat 0 0;}
					}
				}
				.btn{
					&-#{$item}{height: 67px;transition: all 0.3s;}
				}
				.icon{
					&-#{$item}{transition: all 0.3s;}
				}
			}
		}
	}
	//테마
	.theme{position: absolute;top: 0;left: 0;height: 768px;background-color: #3cb4b6;border-radius: 18px 0 0 18px;
		&__area{}
		&__box{margin: 0 28px;}
		&__content{display: flex;margin-top: 10px;}
		&__thumbnail{
			&__items{width: 488px;display: flex;flex-wrap: wrap;align-items: center;margin-top: -12px;}
			&__item{position: relative;margin-top: 12px;
				&:nth-of-type(even){margin-left: 12px;}
				&.active{
					.txt{color: #9ff3f5;background-image: url("/assets/images/club_house_sing_along/popup/theme/bg_thumbnail01_active.png");}
				}
				&.default{
					.txt{color: #0d9699;background-image: url("/assets/images/club_house_sing_along/popup/theme/bg_thumbnail01.png");}
				}
				.txt{position: absolute;bottom: -1px;left: -2px;width: 244px;height: 53px;text-align: center;@include font($comic, $font-b, 21px, 53px, transparent);background: no-repeat 0 100%;transition: all 0.3s;}
				.img{position: relative;}
				.btn{
					&-link{display: block;padding-bottom: 47px;}
				}
			}
		}
		&__normal{margin-left: 12px;
			&__items{border-radius: 15px;overflow: hidden;}
			&__item{position: relative;transition: all 0.3s;
				&.active{
					&:nth-of-type(odd){background-color: #26adaf;}
					&:nth-of-type(even){background-color: #4fced1;}
					.txt{color: #9ff3f5;}
					.icon{
						&-songs{background-image: url("/assets/images/club_house_sing_along/popup/theme/icon_song01_active.png");}
					}
				}
				&.default{
					&:nth-of-type(odd){background-color: #a9edee;}
					&:nth-of-type(even){background-color: #ccf9f9;}
					.txt{color: #0d9699;}
					.icon{
						&-songs{background-image: url("/assets/images/club_house_sing_along/popup/theme/icon_song01.png");}
					}
				}
				.txt{padding-left: 62px;@include font($comic, $font-b, 21px, 65px, transparent);transition: all 0.3s;}
				.img{position: relative;}
				.btn{
					&-link{width: 268px;height: 65px;
					}
				}
				.icon{
					&-songs{display: block;position: absolute;top: 19px;left: 13px;width: 24px;height: 28px;background: no-repeat 0 100%;transition: all 0.3s;}
				}
			}
		}
		.tit{padding: 6px 0 2px 4px;}
		.btn{
			&-link{display: block;position: relative;
				&.active{
					.play{background-image: url("/assets/images/club_house_sing_along/popup/theme/bg_spot_play01_active.png");color: #66d9dc;
						.icon{background-image: url("/assets/images/club_house_sing_along/popup/theme/icon_play01_active.png")}
					}
				}
				.img{display: block;}
				.play{position: absolute;bottom: 4px;right: 24px;padding: 0 26px 0 57px;height: 43px;background: url("/assets/images/club_house_sing_along/popup/theme/bg_spot_play01.png") no-repeat 0 0;@include font($comic, $font-b, 22px, 39px, #0d9699);transition: all 0.3s;
					.icon{position: absolute;top: 12px;left: 17px;width: 18px;height: 20px;background: url("/assets/images/club_house_sing_along/popup/theme/icon_play01.png") no-repeat 0 0;transition: all 0.3s;}
				}
			}
		}
		.icon{
			&-play{}
		}
	}
	//포니스
	.phonics{position: absolute;top: 0;left: 0;height: 768px;border-radius: 18px 0 0 18px;
		&.type01{background-color: #44ab69;
			.phonics{
				&__thumbnail{width: 770px;
					&__item{
						&.active{
							.txt{width: 382px;background-image: url("/assets/images/club_house_sing_along/popup/phonics/type1/bg_thumbnail01_active.png");}
						}
						&.default{
							.txt{width: 382px;background-image: url("/assets/images/club_house_sing_along/popup/phonics/type1/bg_thumbnail01.png");}
						}
						.img{width: 378px;}
					}
				}
				&__normal{display: none;}
			}
		}
		&.type02{background: #44ab69 url("/assets/images/club_house_sing_along/popup/phonics/bg_phonics01.png") no-repeat 100% 590px;
			.phonics{
				&__thumbnail{width: 488px;
					&__item{
						&.active{
							.txt{width: 244px;background-image: url("/assets/images/club_house_sing_along/popup/phonics/type2/bg_thumbnail01_active.png");}
						}
						&.default{
							.txt{width: 244px;background-image: url("/assets/images/club_house_sing_along/popup/phonics/type2/bg_thumbnail01.png");}
						}
						.img{width: 238px;}
					}
				}
				&__normal{margin-left: 12px;width: 270px;}
			}
		}
		&__area{}
		&__box{margin: 0 28px;}
		&__spot{display: flex;margin-top: 3px;
			.btn{
				&-link{position: relative;width: 378px;margin-left: 12px;
					&:first-child{margin-left: 0;}
					&.active{
						.play{background-image: url("/assets/images/club_house_sing_along/popup/phonics/bg_spot_play01_active.png");color: #8ee9af;
							.icon{background-image: url("/assets/images/club_house_sing_along/popup/phonics/icon_play01_active.png")}
						}
					}
					.img{display: block;overflow: hidden;border-radius: 22px}
					.play{position: absolute;bottom: 3px;left: 3px;width: 238px;padding: 0 0 0 49px;height: 43px;box-sizing:border-box; background: url("/assets/images/club_house_sing_along/popup/phonics/bg_spot_play01.png") no-repeat 0 0;@include font($comic, $font-b, 22px, 39px, #00792c);transition: all 0.3s;
						.icon{position: absolute;top: 12px;left: 17px;width: 18px;height: 20px;background: url("/assets/images/club_house_sing_along/popup/phonics/icon_play01.png") no-repeat 0 0;transition: all 0.3s;}
					}
				}
			}
		}
		&__content{display: flex;margin-top: 11px;height: 520px;}
		&__thumbnail{
			&__items{display: flex;flex-wrap: wrap;align-items: center;margin-top: -12px;}
			&__item{position: relative;margin-top: 12px;
				&:nth-of-type(even){margin-left: 12px;}
				&.active{
					.txt{color: #8ee9af;}
				}
				&.default{
					.txt{color: #00792c;}
				}
				.txt{position: absolute;bottom: -1px;left: -2px;height: 53px;text-align: center;@include font($comic, $font-b, 21px, 53px, transparent);background: no-repeat 0 100%;transition: all 0.3s;}
				.img{display: block;position: relative;overflow: hidden;border-radius: 20px 20px 0 0;
					img{max-width: 100%;min-height: 100%;}
				}
				.btn{
					&-link{display: block;padding-bottom: 47px;}
				}
			}
		}
		&__normal{
			&__items{border-radius: 15px;overflow: hidden;}
			&__item{position: relative;transition: all 0.3s;
				&.active{
					&:nth-of-type(odd){background-color: #3ba361;}
					&:nth-of-type(even){background-color: #4ebf77;}
					.txt{color: #8ee9af;}
					.icon{
						&-songs{background-image: url("/assets/images/club_house_sing_along/popup/phonics/icon_songs01_active.png");}
					}
				}
				&.default{
					&:nth-of-type(odd){background-color: #b5ecc4;}
					&:nth-of-type(even){background-color: #c6f3d6;}
					.txt{color: #00792c;}
					.icon{
						&-songs{background-image: url("/assets/images/club_house_sing_along/popup/phonics/icon_songs01.png");}
					}
				}
				.txt{padding-left: 62px;@include font($comic, $font-b, 21px, 65px, transparent);}
				.img{position: relative;}
				.btn{
					&-link{width: 270px;height: 65px;
					}
				}
				.icon{
					&-songs{display: block;position: absolute;top: 19px;left: 13px;width: 24px;height: 28px;background: no-repeat 0 100%;}
				}
			}
		}
		.tit{padding: 10px 0 2px 8px;}
		.icon{
			&-play{}
		}
	}
	//홀리데이
	.holiday{position: absolute;top: 0;left: 0;height: 768px;background-color: #ee9d1f;border-radius: 18px 0 0 18px;
		&__area{}
		&__box{margin: 0 28px;}
		&__content{display: flex;}
		&__normal{display: flex;flex-wrap: wrap;
			&__items{position: relative;overflow: hidden;width: 378px;padding-top: 119px;margin-left: 12px;
				&:nth-of-type(odd){margin-left: 0;}
				&:nth-of-type(n+3){margin-top: 31px;}
				&:nth-of-type(1){background: url("/assets/images/club_house_sing_along/popup/holiday/bg_title01.jpg") no-repeat 0 0;}
				&:nth-of-type(2){background: url("/assets/images/club_house_sing_along/popup/holiday/bg_title02.jpg") no-repeat 0 0;}
				&:nth-of-type(3){background: url("/assets/images/club_house_sing_along/popup/holiday/bg_title03.jpg") no-repeat 0 0;}
				&:nth-of-type(4){background: url("/assets/images/club_house_sing_along/popup/holiday/bg_title04.jpg") no-repeat 0 0;}
			}
			&__item{position: relative;transition: all 0.3s;
				&:nth-of-type(1){border-radius: 15px 15px 0 0;}
				&:nth-last-of-type(1){border-radius: 0 0 15px 15px;}
				&.active{
					&:nth-of-type(odd){background-color: #d8ab66;}
					&:nth-of-type(even){background-color: #c29754;}
					.txt{color: #ffeed0;}
					.icon{
						&-songs{background-image: url("/assets/images/club_house_sing_along/popup/holiday/icon_songs01_active.png");}
					}
				}
				&.default{
					&:nth-of-type(odd){background-color: #ffeed0;}
					&:nth-of-type(even){background-color: #ffdaa1;}
					.txt{color: #b1690e;}
					.icon{
						&-songs{background-image: url("/assets/images/club_house_sing_along/popup/holiday/icon_songs01.png");}
					}
				}
				.txt{padding-left: 100px;@include font($comic, $font-b, 21px, 65px, transparent);transition: all 0.3s;}
				.img{position: relative;}
				.btn{
					&-link{width: 268px;height: 65px;}
				}
				.icon{
					&-songs{display: block;position: absolute;top: 19px;left: 40px;width: 24px;height: 28px;background: no-repeat 0 100%;transition: all 0.3s;}
				}
			}
		}
		.tit{padding: 10px 0 14px 8px;}
		.btn{
			&-link{display: block;position: relative;
				.img{display: block;}
			}
		}
		.icon{
			&-play{}
		}
	}
	.btn{
		&-close{position: absolute;top: 21px;right: 21px;width: 50px;height: 50px;border-radius: 10px;transition: background 0.3s;background-color: transparent;
			//&:hover{background-color: #26adaf;}
		}
	}
	.icon{display: block;width: 100%;height: 100%;
		&-close{display: inline-block;background: url("/assets/images/club_house_sing_along/popup/icon_close01.png") no-repeat 50% 50%;background-size: 34px 34px;}
	}
}